import React, { Component } from "react";
import { Switch } from "@src";

class Demo1 extends Component {
  constructor(){
    super();
    this.state = {
      checked:false
    }
  }

  onChange = () => {
    const {checked} = this.state
    console.log(checked)
    if(checked === false){
      this.setState({
        checked:true
      })
    }else{
      this.setState({
        checked:false
      })
    }
  };

    render() {
      const {checked} = this.state
      return (
        <Switch
          checked={checked}
          onChange={this.onChange}
        />
      );
    }
  }
  
  export default Demo1;